.psm-toggle{width:48px;height:28px;border-radius:50px;display:inline-block;position:relative;cursor:pointer}.psm-toggle>.psm-toggle__switch{background-color:#ffffff;width:24px;height:24px;border-radius:100%;-webkit-box-shadow:0 1px 1px 0 gray;box-shadow:0 1px 1px 0 gray;-webkit-transition:all 0.4s;transition:all 0.4s}.psm-toggle.psm-toggle--inactive{background-color:#dadcdd}.psm-toggle.psm-toggle--inactive>.psm-toggle__switch{position:absolute;top:2px;left:2px}.psm-toggle.psm-toggle--active{background-color:#008ae5;-webkit-transition:all 1s;transition:all 1s}.psm-toggle.psm-toggle--active>.psm-toggle__switch{position:absolute;top:2px;left:22px}
