.psm-button,.psm-button--primary{display:inline-block;position:relative;-webkit-box-sizing:border-box;box-sizing:border-box;min-width:80px;height:36px;padding:0 16px;border:1px solid #e1e1e1;border-radius:3px;color:#212121;background-color:#f8f8f8;font-size:0.875rem;font-weight:600;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-appearance:none;-webkit-tap-highlight-color:rgba(0,0,0,0);overflow:hidden;vertical-align:middle;line-height:34px}.psm-button:active,.psm-button--primary:active{outline:none;border-color:#d4d4d4;background-color:white}.psm-button:hover,.psm-button--primary:hover{cursor:pointer;border-color:#d4d4d4;background-color:white}.psm-button::-moz-focus-inner,.psm-button--primary::-moz-focus-inner{padding:0;border:0}.psm-button:disabled,.psm-button--primary:disabled{cursor:default;pointer-events:none;border-color:#f8f8f8;color:#ababae}.psm-button--primary{background-color:#0078d3;border-color:#0078d3;color:white}.psm-button--primary:active{border-color:#0066b3;background-color:#0066b3}.psm-button--primary:hover{border-color:#0066b3;background-color:#0066b3}.psm-button--primary:disabled{border-color:#66aee5;background-color:#66aee5;color:white}.psm-button--small{min-width:74px;height:24px;line-height:22px}
