.psm-chip,.psm-chip--selected{display:inline-block;padding:2px 8px;border:1px solid #d4d4d4;border-radius:3px;cursor:pointer}.psm-chip:hover,.psm-chip--selected:hover{background-color:#0078d3;color:white}.psm-chip::after,.psm-chip--selected::after{font-family:"prism" !important;content:"";color:#d4d4d4;height:24px;padding:0 0 0 16px;display:inline-block;vertical-align:middle}.psm-chip--selected{-webkit-box-shadow:0 0 0 2px #b3d7f2;box-shadow:0 0 0 2px #b3d7f2}
