.psm-table{width:100%;border:none;border-collapse:collapse}.psm-table .psm-table__tr{border-bottom:1px solid #d4d4d4}.psm-table .psm-table__th{text-align:left;padding:16px;font-size:12px}.psm-table .psm-table__td{text-align:left;padding:16px;line-height:16px}.psm-table-header{font-weight:600;font-size:24px;color:#212121;padding-top:16px;padding-bottom:24px;float:left}.psm-table__actions{float:right;padding-top:8px;padding-bottom:24px}.psm-table--box{border-radius:3px;-webkit-box-shadow:0 0 1px 0 rgba(26,30,32,0.2),0 1px 3px 0 rgba(26,30,32,0.1);box-shadow:0 0 1px 0 rgba(26,30,32,0.2),0 1px 3px 0 rgba(26,30,32,0.1)}.psm-table--box .psm-table-header,.psm-table--box .psm-table__actions{padding-left:24px;padding-right:24px}.psm-table--box .psm-table__th,.psm-table--box .psm-table__td{padding-left:24px;padding-right:24px}
