.psm-radio{display:inline-block;position:relative;-webkit-box-flex:0;-ms-flex:0 0 auto;flex:0 0 auto;-webkit-box-sizing:border-box;box-sizing:border-box;width:20px;height:20px;padding:10px;cursor:pointer}.psm-radio::after{background-color:#fff;width:16px;height:16px;border-radius:16px;top:0;left:0;position:relative;content:"";display:inline-block;visibility:visible;border:1px solid #8e8e8e}.psm-radio:hover::after{background-color:#fff;width:16px;height:16px;border-radius:16px;top:0;left:0;position:relative;content:"";display:inline-block;visibility:visible;border:1px solid #0078d3}.psm-radio:checked::after{background-color:#fff;width:4px;height:4px;border-radius:16px;top:0;left:0;position:relative;content:"";display:inline-block;visibility:visible;border:7px solid #0078d3}
