.psm-input{font-family:"ProximaNova-Reg","Helvetica","Arial",sans-serif;outline:none;width:100%;height:36px;margin:3px 0;padding:6px 16px;font-size:0.875rem;-webkit-box-sizing:border-box;box-sizing:border-box;border:1px solid #e1e1e1;border-radius:3px}.psm-input:hover{border-color:#42a5f5;-webkit-box-shadow:0 0 0 2px #e3f2fd;box-shadow:0 0 0 2px #e3f2fd}.psm-input.psm-input--error,.psm-input.psm-input--active:invalid{border-color:#f44923;-webkit-box-shadow:0 0 0 2px rgba(244,73,35,0.2);box-shadow:0 0 0 2px rgba(244,73,35,0.2)}.psm-select{line-height:36px;background-color:white;-webkit-appearance:none;-moz-appearance:none;appearance:none;display:inline-block;background-image:url(./icons/psm-select-arrow.svg);background-position:right 9px;background-repeat:no-repeat}.psm-multi-select,.psm-multi-select--selected{display:inline-block;padding:2px 8px;border:1px solid #d4d4d4;border-radius:3px;cursor:pointer}.psm-multi-select:hover,.psm-multi-select--selected:hover{background-color:#0078d3;color:white}.psm-multi-select::before,.psm-multi-select--selected::before{content:"";background-color:#ffffff;height:12px;width:12px;border:1px solid #d4d4d4;padding:0;margin:0 16px 0 0;display:inline-block;vertical-align:middle}.psm-multi-select--radio::before{border-radius:100%}.psm-multi-select--selected{border:1px solid #0078d3;-webkit-box-shadow:0 0 0 2px #b3d7f2;box-shadow:0 0 0 2px #b3d7f2}.psm-multi-select--selected:hover::before{background-color:transparent;color:#ffffff}.psm-multi-select--selected::before{font-family:"prism" !important;content:"";border:none;color:#0078d3;height:24px;padding:0 0 0 0;display:inline-block;vertical-align:middle}
