.content{max-width:800px;margin:0 auto 3em}.section-header{background-color:#f8f8f8;margin-bottom:60px;padding:10px;text-align:center}.types dt{float:left}.types dd{margin-left:200px;height:6em}.buttons{margin-bottom:60px}.narrow{display:inline-block;width:300px;margin:20px 0 60px}.narrow input:not([type="checkbox"]):not([type="radio"]),.narrow textarea,.narrow select{margin-bottom:20px}.center{text-align:center}ul{list-style:none;margin:0 0 60px;padding:0}ul li{display:inline-block;width:24%;margin-bottom:2em;text-align:center}h1,h2,h3,h4,h5,h6,p{margin:0;padding:0}.flex{display:-webkit-box;display:-ms-flexbox;display:flex}.flex div{-webkit-box-flex:1;-ms-flex:1;flex:1}
